import uniSearchBar from '@/components/uni-search-bar/uni-search-bar.vue'
export default {
	name: "",
	components: {
		uniSearchBar
	},
	props: {},
	data() {
		return {
			keywords: "",
			page: 1,
			pageSize: 12,
			searList: [],
			historyList: [],
			initialVal: ''

		}
	},
	methods: {
		// 点击搜索
		searchItem(item) {
			this.initialVal = item
			setTimeout(() => {
				this.initialVal = ''
			}, 1000)

		},
		// 跳转到详情页
		goDetails(id) {
			uni.navigateTo({
				url: `../details/details?id=${id}`
			});
		},
		input(e) {
			// console.log(e)
		},
		// 删除搜索历史
		delHistory() {
			uni.showModal({
				title: '是否确认删除所以搜索历史？',
				content: '',
				showCancel: true,
				cancelText: '取消',
				cancelColor: '#000000',
				confirmText: '确定',
				confirmColor: '#3CC51F',
				success: (result) => {
					if (result.confirm) {
						this.historyList = []
						wx.removeStorageSync('historyList');
					}
				},
				fail: () => {},
				complete: () => {}
			});
		},
		cancel() {
			this.keywords = {
				value: ''
			}
		},
		// 获取猜你喜欢数据
		getSearchList(e) {
			uni.showLoading()
			uni.request({
				url: `https://www.imovietrailer.com/superhero/search/list?qq=2684425594&keywords=${e}&page=${this.page}&pageSize=${this.pageSize}`,
				method: 'POST',
				data: {
					qq: 2684425594,
					type: 'superhero'
				},
				header: {
					'custom-header': 'application/json' //自定义请求头信息
				},
				success: res => {
					if (res.statusCode === 200) {
						if (res.data.data.rows.length === 0) {
							if (this.page === 1) {
								uni.showToast({
									title: '暂无此数据',
									duration: 2000,
									icon: "none"
								})
							} else {
								uni.showToast({
									title: '没有更多数据了',
									duration: 2000,
									icon: "none"
								})
							}
						} else {
							if (this.page === 1) {
								this.saveHistory(e)
							}
							this.page++;
							this.searList = this.searList.concat(res.data.data.rows)

						}

					}
				},
				fail: () => {},
				complete: () => {
					uni.hideLoading()
				}
			})
		},
		// 存搜索历史
		saveHistory(val) {
			let arr = []
			if (uni.getStorageSync('historyList')) {
				arr = uni.getStorageSync('historyList')
				if (!JSON.stringify(arr).includes(val)) {
					arr.unshift(val)
					this.historyList.unshift(val)
					uni.setStorageSync('historyList', arr);
				}
			} else {
				arr.unshift(val)
				this.historyList.unshift(val)
				uni.setStorageSync('historyList', arr);
			}
		}
	},

	mounted() {

	},
	onLoad() {
		if (uni.getStorageSync('historyList')) {
			this.historyList = wx.getStorageSync('historyList')
		}
	},
	onReachBottom() {
		if (this.keywords.value.trim().length > 0) {
			this.getSearchList(this.keywords.value.trim())
		}
	},
	filters: {

	},
	computed: {

	},
	watch: {
		keywords(val) {
			if (val.value.trim().length > 0) {
				this.page = 1
				this.getSearchList(val.value.trim())
			} else {
				this.searList = []
			}
		}
	},
	directives: {

	},
	onNavigationBarSearchInputChanged(e) {
		console.log(e)
	}
}
